import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Card, CardBody, CardHeader, Col, CustomInput, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType, isEmpty } from 'summer';
import { EmpresaTelefone } from '../../../../models/dtos/ger/empresaTelefone';
import { tipoTelefoneSelection } from '../../../../utilities/tipoTelefoneFormatter';

const EmpresaTelefoneDetailEditView: FC<DetailEditViewFormProps<EmpresaTelefone>> = props => {
  const formProps = useForm<EmpresaTelefone>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  const validateExtras = (formValues, currentValues, parentObject) => {
    const isInsert = isEmpty(formValues.rowIndex) && isEmpty(formValues.id);
    const erros = [];

    if (!tipoTelefoneSelection(formProps.getValues())) {
      erros.push('Pelo menos um tipo de telefone deve ser marcado.');
    }

    if (parentObject.telefones && formProps.getValues().tipoTelefonePrincipal === true) {
      let foundTelefones;
      if (isInsert) {
        foundTelefones = parentObject.telefones.filter(it => it.tipoTelefonePrincipal);
      } else {
        foundTelefones = parentObject.telefones.filter(it => {
          const hasId = !isEmpty(formValues.id);
          if (hasId) {
            return it.tipoTelefonePrincipal && Number(formValues.id) !== Number(it.id);
          } else {
            return it.tipoTelefonePrincipal && Number(formValues.rowIndex) !== Number(it.rowIndex);
          }
        });
      }
      if (foundTelefones && foundTelefones.length > 0) {
        erros.push('J existe um Telefone cadastrado como principal para a Empresa.');
      }
    }
    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <InputGroupInline formProps={{ ...formProps, validation: { maxLength: 60 } }} id="contato" isPrependLabel={true} label="Contato" />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="numero"
            isPrependLabel={true}
            label="Telefone"
            type={InputType.TELEFONE}
          />
        </Col>
      </Row>
      <Row>
        <Col style={{ marginBottom: '30px' }}>
          <Card>
            <CardHeader className="font-weight-bold bg-light">Tipo</CardHeader>
            <CardBody>
              <Row style={{ marginBottom: '10px' }}>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Principal"
                    name="tipoTelefonePrincipal"
                    id="tipoTelefonePrincipal"
                    innerRef={formProps.register}
                  />
                </Col>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Residencial"
                    name="tipoTelefoneResidencial"
                    id="tipoTelefoneResidencial"
                    innerRef={formProps.register}
                  />
                </Col>
              </Row>
              <Row style={{ marginBottom: '10px' }}>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Comercial"
                    name="tipoTelefoneComercial"
                    id="tipoTelefoneComercial"
                    innerRef={formProps.register}
                  />
                </Col>
                <Col>
                  <CustomInput type="switch" label="Celular" name="tipoTelefoneCelular" id="tipoTelefoneCelular" innerRef={formProps.register} />
                </Col>
              </Row>
              <Row>
                <Col>
                  <CustomInput type="switch" label="Fax" name="tipoTelefoneFax" id="tipoTelefoneFax" innerRef={formProps.register} />
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default EmpresaTelefoneDetailEditView;
