import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Card, CardBody, CardHeader, Col, CustomInput, FormGroup, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, isEmpty } from 'summer';
import useCepManager from '../../../../hooks/useCepManager';
import { Endereco } from '../../../../models/dtos/ger/endereco';
import { tipoEnderecoSelection } from '../../../../utilities/tipoEnderecoFormatter';
import CepBairroIS from '../../../components/ger/cepBairroIS/CepBairroIS';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';
import CepLogradouroIS from '../../../components/ger/cepLogradouro/CepLogradouroIS';
import CepTituloIS from '../../../components/ger/cepTituloIS/CepTituloIS';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

const EmpresaEnderecoDetailEditView: FC<DetailEditViewFormProps<Endereco>> = props => {
  const formProps = useForm<Endereco>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  const cepManager = useCepManager(formProps, {
    bairro: true,
    cep: true,
    localidade: true,
    nome: 'logradouro',
    titulo: 'tipoLogradouro',
    uf: true,
  });

  useEffect(
    () => {
      if (props.selectedItem && props.selectedItem.id) {
        setTimeout(() => {
          // @ts-ignore
          formProps.setValue('cepFmt', { cep: props.selectedItem.cep, label: props.selectedItem.cep }, true);
        }, 800);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const validateExtras = (formValues, currentValues, parentObject) => {
    const isInsert = isEmpty(formValues.rowIndex) && isEmpty(formValues.id);
    const erros = [];

    if (!tipoEnderecoSelection(formProps.getValues())) {
      erros.push('Pelo menos um tipo de endereo deve ser marcado.');
    }

    if (parentObject.enderecos && formProps.getValues().tipoEnderecoPrincipal === true) {
      let foundEndereco;
      if (isInsert) {
        foundEndereco = parentObject.enderecos.filter(it => it.tipoEnderecoPrincipal);
      } else {
        foundEndereco = parentObject.enderecos.filter(it => {
          const hasId = !isEmpty(formValues.id);
          if (hasId) {
            return it.tipoEnderecoPrincipal && Number(formValues.id) !== Number(it.id);
          } else {
            return it.tipoEnderecoPrincipal && Number(formValues.rowIndex) !== Number(it.rowIndex);
          }
        });
      }
      if (foundEndereco && foundEndereco.length > 0) {
        erros.push('J existe um Endereo cadastrado como principal.');
      }
    }

    return erros;
  };

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps} validateExtras={validateExtras}>
      <Row>
        <Col>
          <CepLogradouroIS
            formProps={{ ...formProps, validation: { required: true } }}
            id="cepFmt"
            isPrependLabel={true}
            label="CEP"
            placeholder="Informe o CEP"
            cepManager={cepManager}
          />
          <input type="hidden" name="cep" ref={formProps.register} />
        </Col>
        <Col>
          <CepTituloIS id="tipoLogradouro" isPrependLabel={true} formProps={{ ...formProps, validation: { required: true } }} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, maxLength: 150 } }}
            id="logradouro"
            isPrependLabel={true}
            label="Logradouro"
            placeholder="Informe o logradouro"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, maxLength: 10 } }}
            id="numero"
            isPrependLabel={true}
            label="Numero"
            placeholder="Informe o nmero"
          />
        </Col>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 60 } }}
            id="complemento"
            isPrependLabel={true}
            label="Complemento"
            placeholder="Informe o complemento"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CepUFIS formProps={{ ...formProps, validation: { required: true } }} id="uf" />
        </Col>
        <Col>
          <CepLocalidadeIS formProps={{ ...formProps, validation: { required: true } }} id="localidade" />
        </Col>
      </Row>
      <Row>
        <Col>
          <CepBairroIS formProps={{ ...formProps, validation: { required: true } }} id="bairro" />
        </Col>
      </Row>
      <Row>
        <Col>
          <Card style={{ marginBottom: '16px', borderWidth: 'thin' }}>
            <CardHeader className="font-weight-bold bg-light">Tipo de Endereo</CardHeader>
            <CardBody>
              <FormGroup tag={Row}>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Principal"
                    name="tipoEnderecoPrincipal"
                    id="tipoEnderecoPrincipal"
                    innerRef={formProps.register}
                  />
                </Col>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Residencial"
                    name="tipoEnderecoResidencial"
                    id="tipoEnderecoResidencial"
                    innerRef={formProps.register}
                  />
                </Col>
              </FormGroup>
              <FormGroup tag={Row}>
                <Col>
                  <CustomInput type="switch" label="Cobrana" name="tipoEnderecoCobranca" id="tipoEnderecoCobranca" innerRef={formProps.register} />
                </Col>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Comercial"
                    name="tipoEnderecoComercial"
                    id="tipoEnderecoComercial"
                    innerRef={formProps.register}
                  />
                </Col>
              </FormGroup>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default EmpresaEnderecoDetailEditView;
