import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { EmpresaEmail } from '../../../../models/dtos/ger/empresaEmail';

const EmpresaEmailDetailEditView: FC<DetailEditViewFormProps<EmpresaEmail>> = props => {
  const formProps = useForm<EmpresaEmail>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  const defaultWidth = 100;
  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 60 } }}
            id="contato"
            isPrependLabel={true}
            label="Contato"
            labelWidth={defaultWidth}
            placeholder="Informe o contato"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 120, required: true } }}
            id="email"
            isPrependLabel={true}
            label="Email"
            labelWidth={defaultWidth}
            placeholder="Informe o email"
            type={InputType.EMAIL}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default EmpresaEmailDetailEditView;
