import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Alert, Col, Form, Row } from 'reactstrap';
import { EditViewProps, ERROR, getSelectedItem, InputGroupInline, InputType, ProcessBar, showToast, WindowComponent } from 'summer';
import { AutomatizacaoConfigEmpresa } from '../../../../models/dtos/ger/AutomatizacaoConfigEmpresa';
import { TIPO_EMPRESA_AUTOMATIZACAO_VALUES } from '../../../../models/enumerated/ger/tipoEmpresaEnum';
import { LUCRO_REAL, TIPO_TRIBUTACAO_ENUM_VALUES } from '../../../../models/enumerated/ger/tipoTributacaoEnum';
import { ANUAL, FORMA_APURACAO_ENUM_VALUES } from '../../../../models/enumerated/mcg/formaApuracaoEnum';
import { configurarEmpresa } from '../../../../services/ger.service';
import { closeLoading, showLoading } from '../../../../utilities/empresa.util';

interface EmpresaConfiguracaoAutomaticaProps extends EditViewProps<AutomatizacaoConfigEmpresa> {}

interface ConfiguracaoEmpresaProps {
  empresaAutomacao?: AutomatizacaoConfigEmpresa;
}

const EmpresaConfiguracaoAutomaticaProcessView: FC<EmpresaConfiguracaoAutomaticaProps & RouteComponentProps & ConfiguracaoEmpresaProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<AutomatizacaoConfigEmpresa>({ defaultValues: selectedItem, mode: 'onBlur' });
  const dispatch = useDispatch();
  const [isLucroReal, setLucroReal] = useState(false);

  useEffect(
    () => {
      setTimeout(() => {
        formProps.reset({
          id: props.empresaAutomacao.id,
          razaoSocial: props.empresaAutomacao.razaoSocial,
          tipoEmpresa: props.empresaAutomacao.tipoEmpresa,
          tipoTributacao: props.empresaAutomacao.tipoTributacao,
        });

        if (props.empresaAutomacao.tipoTributacao === LUCRO_REAL.value) {
          setLucroReal(true);
          formProps.setValue('formaApuracaoECF', ANUAL.value, true);
        }
      }, 800);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  function handleImport(): void {
    const empresa = formProps.getValues();

    dispatch(showLoading());

    configurarEmpresa(empresa, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
        dispatch(closeLoading());
      },
      thenFunction: result => {
        dispatch(closeLoading());
        props.history.go(-2);
      },
    });
  }

  return (
    <WindowComponent title="Automatizao de cadastro da Empresa" wikiId={6574}>
      <Form>
        <InputGroupInline formProps={formProps} id="id" hidden={true} />
        <InputGroupInline disabled={true} formProps={formProps} id="razaoSocial" isPrependLabel={true} label="Empresa" />

        <Alert color="primary" className="text-center">
          Esta funcionalidade visa otimizar o processo de cadastramento e configurao de sua empresa a partir das melhores prticas utilizadas no
          mercado. Facilitando assim a gerao de arquivos digitais, relatrios, impostos, dentre outras funcionalidades. A configurao levar em
          considerao cada tipo de empresa selecionada dentre as opes abaixo.
        </Alert>
        <Alert color="danger" className="text-center">
          ATENO! As configuraes automticas no substituem a importncia e o dever da conferncia por parte do Contador das alquotas e valores
          gerados como padro, devido  natureza e particularidades inerentes a cada empresa.
        </Alert>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="tipoEmpresa"
              inputDropDownProps={{
                optionsProvider: TIPO_EMPRESA_AUTOMATIZACAO_VALUES,
              }}
              isPrependLabel={true}
              label="Tipo de Empresa"
              placeholder="Selecione o Tipo de Empresa"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              disabled={true}
              formProps={formProps}
              id="tipoTributacao"
              inputDropDownProps={{
                optionsProvider: TIPO_TRIBUTACAO_ENUM_VALUES,
              }}
              isPrependLabel={true}
              label="Tributao"
              placeholder="Selecione o Tipo de CAEPF"
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        {isLucroReal ? (
          <Row>
            <Col>
              <InputGroupInline
                formProps={formProps}
                id="formaApuracaoECF"
                inputDropDownProps={{
                  optionsProvider: FORMA_APURACAO_ENUM_VALUES,
                }}
                isPrependLabel={true}
                label="ECF forma de apurao"
                placeholder="Selecione a Forma de apurao a ser utilizada nas configuraes do SPED ECF"
                type={InputType.DROP_DOWN}
              />
            </Col>
          </Row>
        ) : null}
        <ProcessBar handleSubmit={formProps.handleSubmit(handleImport)} hideCancelButton={true} />
      </Form>
    </WindowComponent>
  );
};

export default EmpresaConfiguracaoAutomaticaProcessView;
